.FlowerControls{
    width: 100%;
    background-image: -webkit-gradient(linear, right top, left bottom, from(#f3ede1), to(#916404));
    background-image: -webkit-linear-gradient(top right, #f3ede1, #916404);
    background-image: -o-linear-gradient(top right, #f3ede1, #916404);
    background-image: linear-gradient(to bottom left, #f3ede1, #916404);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow:row;
        flex-flow:row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-shadow: 0 .5rem 1rem #ccc;
            box-shadow: 0 .5rem 1rem #ccc;
    margin: auto;
    padding: 10px 0;
    border-radius: 15px;
    margin-bottom: 4rem;
    grid-column: center-start/center-end;
}
.Orderdata{
    padding: 2rem 0;
    width: 45%;
}

.Heading{
    font-size: 4rem;
    font-weight: 700;
    letter-spacing: 1px;
    text-align: left;
    color: #fff;
}
.Ordertext{
    font-size: 1.8rem;
    font-weight: 600;
    line-height: 1.63;
    letter-spacing: .3px;
    text-align: left;
    color: #fff;
    padding-left: 0;
}
.Buy{
    padding: 2rem 0;
text-align: center;
}
.Total{
    font-size: 2rem;
    padding:2rem;
    text-decoration: underline;
    font-weight: bolder;
    font-family: 'El Messiri', sans-serif;

}

.OrderButton {
    outline: none;
    cursor: pointer;
    /* color: #966909; */
    font-family: inherit;
    font-size: 1.8rem;
    padding: 15px 30px;

    font-size: 1.9rem;
    font-weight: 300;
    border: none;
    text-transform: uppercase;
    border-radius: 100px;
    background-image: -webkit-gradient(linear, left top, right top, from(#996a05), to(#1a1201));
    background-image: -webkit-linear-gradient(left, #996a05, #1a1201);
    background-image: -o-linear-gradient(left, #996a05, #1a1201);
    background-image: linear-gradient(to right, #996a05, #1a1201);
    color: #fff;
    overflow: hidden;
}

.OrderButton:hover, .OrderButton:active {
    background-image: -webkit-gradient(linear, left top, right top, from(#966909), to(#3d2b05));
    background-image: -webkit-linear-gradient(left, #966909, #3d2b05);
    background-image: -o-linear-gradient(left, #966909, #3d2b05);
    background-image: linear-gradient(to right, #966909, #3d2b05);
    color: #ccc;
}
.OrderButton:active {
    -webkit-transform: translateY(2px);
        -ms-transform: translateY(2px);
            transform: translateY(2px);
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: whitesmoke;
}

.OrderButton:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
    color: whitesmoke;

}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


