.NavigationItem{
   margin: 10px 0;
   -webkit-box-sizing: border-box;
           box-sizing: border-box;
   display: block;
   width: 100%;
}

.NavigationItem a{
      color: #ccc;
    font-size: 1.8rem;
    width: 100%;
    text-decoration: none;
  
    -webkit-box-sizing: border-box;
  
            box-sizing: border-box;
    display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    color: #ecea52;
    background-color: #703B09;
}

@media(min-width:500px){
    .NavigationItem{
        margin: 0;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
        height: 100%;
        width: auto;
     }
     
     .NavigationItem a{
            color: #ccc;
         height: 100%;
         border-bottom: 4px solid transparent;
         padding: 1.6rem 1rem;
        
     }
     
     .NavigationItem a:hover,
     .NavigationItem a:active,
     .NavigationItem a.active{
         background-color: rgb(179, 129, 69);
         color: white;
         border-bottom: 4px solid #f5ff41;
     }
}


